$dark-color:  #5a1d1af5;
$dark-transparent: rgba(90, 29, 26, 0.88);
$light-color: #eee6e6;
$light-transparent: #eee6e691;

.navigation {
  display: flex;
  margin-bottom: 10px;
  flex-direction: column;
  justify-content: center;
  height: 11vh;
  background-color: rgba(255, 255, 255, 0.8);
}

.list {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: $dark-transparent;

  &__link {
    height: 48px;
    line-height: 48px;
    font-size: 22px;
    text-decoration: none;
    text-transform: uppercase;
  }
}

.page {
  border: 0 solid transparent;
}

.content {
  height: 100vh;
}
