$dark-color:  #5a1d1af5;
$dark-transparent: rgba(90, 29, 26, 0.88);
$light-color: #eee6e6;
$light-transparent: #eee6e691;

.library {
  margin: 10px auto;
  width: 44vw;
  background-color: $dark-transparent;
  border: 1px solid transparent;
  border-radius: 50px;
  overflow: hidden;

  &__element {
    margin-bottom: 6vh;
    padding: 2vh;
    background-color: $dark-transparent;
    text-align: center;
    list-style-type: none;

    &:first-child {
      margin-top: 6vh;
    }
  }
}

.breadcrumbs {
  &__container {
    margin: auto;
    padding: 1vmin 2vmin 1vmin 14vmin;
    display: flex;
    align-items: center;
    background-color: $dark-transparent;
  }

  &__container * {
    font-size: 16px;
    line-height: 16px;
    color: $light-color;
    text-decoration: none;
    cursor: pointer;
  }
}

.itemLink {
  display: block;
  font-size: 24px;
  color: $light-color;
  text-decoration: none;
}

.title {
  margin: 5vh 0;
  padding: 15px;
  text-align: center;

  &--small {
    margin: 0;
  }
}

.download-link {
  width: fit-content;
  margin: 5vh auto;
  display: flex;
  font-size: 20px;
  background-color: transparent;

  &--small {
    margin: 5vh auto;
  }
}

.player {
  margin: 5vh auto;
  display: flex;
  background-color: transparent;

  &--small {
    margin: 5vh auto;
  }
}
